@import 'https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,600i';
.Products, .AdminNav-container, .Admin-container, .PackagingList, .ProductList {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 40px;
  list-style: none;
}

@media (max-width: 639px) {
  .Products, .AdminNav-container, .Admin-container, .PackagingList, .ProductList {
    padding: 0 15px;
  }
}

.ProductAdded,
.ProductDetail, .Cart, .Orders, .OrderDetail {
  width: 100%;
  max-width: 760px;
  margin: 0 auto;
  padding: 40px;
  border-radius: 7px;
  background-color: #fff;
}

@media (max-width: 639px) {
  .ProductAdded,
  .ProductDetail, .Cart, .Orders, .OrderDetail {
    padding: 20px;
    border-radius: 0;
  }
}

@media (min-width: 640px) {
  .ProductAdded,
  .ProductDetail, .Cart, .Orders, .OrderDetail {
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
  }
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  background-color: #f7f7f7;
  overflow: scroll;
}

body.main {
  padding: 0 0 100px;
}

@media (max-width: 639px) {
  body.main {
    background-color: #fff;
    padding-top: 130px;
  }
}

@media (min-width: 640px) {
  body.main {
    padding: 160px 40px 100px;
  }
  body.main.products_page, body.main.product_page {
    background-color: #333;
  }
  body.main.checkout_page {
    background-color: #ec652a;
  }
}

