*,
::before,
::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-family: "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
  line-height: 1.4;
  height: 100%;
  background-color: #fff;
  color: #444444;
}

body {
  margin: 0;
  background-color: #fff;
  color: #444444;
}

a,
.link {
  color: #08c;
  text-decoration: none;
}

a:hover,
.link:hover {
  text-decoration: underline;
  color: #08c;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, .Login-title, h5, .h5, h6, .h6 {
  font-family: "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
  font-weight: 700;
  line-height: 1.2;
  color: #444444;
}

h1, .h1 {
  font-size: 1.61051rem;
}

@media screen and (min-width: 320px) {
  h1, .h1 {
    font-size: calc(1.61051rem + 33.7009049326 * (100vw - 320px) / 680);
  }
}

@media (min-width: 1000px) {
  h1, .h1 {
    font-size: 3.7168165583rem;
  }
}

h2, .h2 {
  font-size: 1.4641rem;
}

@media screen and (min-width: 320px) {
  h2, .h2 {
    font-size: calc(1.4641rem + 23.40043538 * (100vw - 320px) / 680);
  }
}

@media (min-width: 1000px) {
  h2, .h2 {
    font-size: 2.9266272113rem;
  }
}

h3, .h3 {
  font-size: 1.331rem;
}

@media screen and (min-width: 320px) {
  h3, .h3 {
    font-size: calc(1.331rem + 15.574894 * (100vw - 320px) / 680);
  }
}

@media (min-width: 1000px) {
  h3, .h3 {
    font-size: 2.304430875rem;
  }
}

h4, .h4, .Login-title {
  font-size: 1.21rem;
}

@media screen and (min-width: 320px) {
  h4, .h4, .Login-title {
    font-size: calc(1.21rem + 9.6722 * (100vw - 320px) / 680);
  }
}

@media (min-width: 1000px) {
  h4, .h4, .Login-title {
    font-size: 1.8145125rem;
  }
}

h5, .h5 {
  font-size: 1.1rem;
}

@media screen and (min-width: 320px) {
  h5, .h5 {
    font-size: calc(1.1rem + 5.26 * (100vw - 320px) / 680);
  }
}

@media (min-width: 1000px) {
  h5, .h5 {
    font-size: 1.42875rem;
  }
}

h6, .h6 {
  font-size: 1rem;
}

@media screen and (min-width: 320px) {
  h6, .h6 {
    font-size: calc(1rem + 2 * (100vw - 320px) / 680);
  }
}

@media (min-width: 1000px) {
  h6, .h6 {
    font-size: 1.125rem;
  }
}

img {
  max-width: 100%;
}

.Product {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px 10px 20px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  color: inherit;
}

.Product, .Product:hover {
  text-decoration: none;
}

.Product-image {
  padding-top: 70%;
  background: #eaeaea 50% 50%;
  background-size: cover;
  border-radius: 2px;
}

.Product-info {
  text-align: center;
}

.Product-name {
  margin: 20px 0 10px;
  font-size: 15px;
  font-weight: 600;
}

.Product-description {
  margin: 0;
  padding: 0 10px;
  font-size: 13px;
  color: #444;
}

.Products-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
  margin: 0 0 0 -30px;
  list-style: none;
}

.Products-item {
  display: flex;
  width: 100%;
  max-width: 300px;
  padding: 0 0 30px 30px;
}

.ProductAdded,
.ProductDetail {
  max-width: 690px;
}

.ProductDetail-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ccc;
}

.ProductAdded-title {
  flex-grow: 1;
  color: #666;
}

.ProductDetail-imageWrap {
  flex-shrink: 0;
  width: 15%;
}

.ProductDetail-image {
  padding-top: 100%;
  background-size: cover;
  background-color: #eaeaea;
  border-radius: 5px;
}

.ProductDetail-info {
  flex-grow: 1;
  padding: 10px 20px;
}

.ProductDetail-name {
  margin: 0;
  font-size: 17px;
  font-weight: 600;
}

.ProductDetail-description {
  margin: 0;
  font-size: 14px;
  color: #444;
}

.ProductDetail-optionGroup {
  margin-bottom: 20px;
}

.ProductDetail-optionLabel {
  margin-bottom: 10px;
  display: inline-block;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 600;
  color: #616161;
}

.Cart-title > p {
  margin-top: 0;
}

.Cart-list {
  margin-bottom: 40px;
  padding: 0;
  border-top: 1px solid #e6e6e6;
  list-style: none;
}

.Cart-item {
  padding: 15px 0;
  border-bottom: 1px solid #e6e6e6;
}

@media (max-width: 639px) {
  .Cart-empty {
    padding-top: 20px;
  }
}

.CartItem {
  display: flex;
  margin-left: -10px;
  flex-direction: row;
  align-items: center;
}

.CartItem > div {
  padding-left: 10px;
}

.CartItem-qty {
  min-width: 80px;
}

.CartItem-imageWrap {
  width: 25%;
  max-width: 90px;
}

.CartItem-info {
  flex-grow: 1;
}

.CartItem-name {
  margin: 0;
  font-size: 15px;
  font-weight: 600;
}

.CartItem-description {
  margin: 0;
  font-size: 13px;
  color: #808080;
}

.CartItem-image {
  padding-top: 100%;
  background-size: cover;
  background-color: #eaeaea;
  border-radius: 5px;
}

.CartItem-removeLink {
  font-size: 14px;
  text-decoration: underline;
  cursor: pointer;
}

.CartItem-removeLink, .CartItem-removeLink:hover {
  color: #cc001d;
}

@media (max-width: 639px) {
  .CartItem {
    flex-wrap: wrap;
  }
  .CartItem-imageWrap {
    flex-shrink: 0;
    min-width: 0;
  }
  .CartItem-info {
    flex-shrink: 0;
    width: 75%;
    padding-left: 20px;
  }
  .CartItem-qty {
    margin-top: 10px;
  }
  .CartItem-remove,
  .CartItem-qty {
    width: 50%;
  }
  .CartItem-qty .Form-control {
    height: 30px;
    font-size: 14px;
  }
  .CartItem-remove {
    text-align: right;
  }
  .CartItem-removeLink {
    display: inline-block;
    padding: 7px;
  }
}

@media (min-width: 640px) {
  .CartItem-remove {
    padding-left: 20px;
    white-space: nowrap;
  }
}

.NavBar {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #ec652a;
  color: #fff;
}

.NavBar-container {
  display: flex;
  align-items: center;
  height: 80px;
  max-width: 1180px;
  margin: 0 auto;
  padding: 10px 20px;
}

.NavBar-logo {
  flex-grow: 1;
  text-align: center;
}

.NavBar-logoImage {
  height: 65px;
}

.NavBar-back {
  flex-shrink: 0;
  width: 110px;
}

.NavBar-account {
  flex-shrink: 0;
  width: 110px;
  text-align: right;
}

.NavBar-link {
  position: relative;
  display: inline-block;
  color: #fff;
  text-decoration: none;
}

.NavBar-link, .NavBar-link:hover {
  color: #fff;
}

.NavBar-link--account {
  margin-right: 8px;
}

.Navbar-cartQty {
  position: absolute;
  z-index: 2;
  top: -8px;
  right: -8px;
  display: block;
  box-sizing: content-box;
  width: 20px;
  height: 15px;
  border: 1px solid #8d2dea;
  line-height: 15px;
  font-size: 12px;
  border-radius: 8px;
  background: #ec652a;
  text-align: center;
  overflow: hidden;
}

.NavBar-icon {
  height: 17px;
  width: 20px;
  margin-right: 4px;
  margin-top: -4px;
  vertical-align: middle;
  fill: #fff;
}

@media (max-width: 420px) {
  .NavBar-back,
  .NavBar-account {
    width: 50%;
  }
  .NavBar-logo {
    position: absolute;
    top: 40px;
    left: 50%;
    width: 106px;
    margin-left: -53px;
  }
  .NavBar-logoImage {
    height: 55px;
  }
}

.ProductAdded {
  position: relative;
}

.CloseButton {
  position: absolute;
  top: -10px;
  right: -10px;
  cursor: pointer;
}

@media (max-width: 639px) {
  .CloseButton {
    top: 10px;
    right: 10px;
  }
}

.CloseButton-image {
  height: 40px;
  width: 40px;
}

.CheckoutForm-wrap {
  padding: 5px;
  border-radius: 6px;
  background-color: #e3e3e3;
}

.Form-group {
  margin-bottom: 20px;
}

.Form-label {
  display: block;
  margin-bottom: .5rem;
  font-weight: 700;
}

.Form-control {
  height: 46px;
  width: 100%;
  padding: 0 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 17px;
  background-color: #fff;
  color: #555;
}

textarea.Form-control {
  max-height: 160px;
  min-height: 38px;
}

.Form-horizontal, .CheckoutForm {
  display: flex;
  margin-left: -8px;
}

.Form-horizontal > .Form-group, .CheckoutForm > .Form-group {
  margin-bottom: 0;
  padding-left: 8px;
}

.Form-inputList {
  list-style: none;
}

.Form-inputList > li {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.Form-inputList > li:first-child {
  margin-top: 0;
}

.Form-inputList > li > .Form-deleteItem {
  width: 20px;
  margin-left: 10px;
  text-align: right;
  flex-shrink: 1;
}

.Form-inputList--qty {
  max-width: 180px;
}

.Form-inputList--qty .Form-control {
  height: 32px;
  font-size: 15px;
}

.Form-deleteIcon {
  width: 18px;
  height: 18px;
  fill: #7d7d7d;
  cursor: pointer;
}

.Form-deleteIcon:hover {
  fill: #333;
}

.CheckoutForm--email {
  flex-grow: 1;
}

@media (max-width: 639px) {
  .CheckoutForm {
    flex-direction: column;
    margin-top: -5px;
  }
  .CheckoutForm .Form-group {
    margin-top: 5px;
  }
}

.Orders {
  max-width: 960px;
}

.Orders-list {
  width: 100%;
  border-top: 1px solid #ccc;
  border-spacing: 0;
  border-collapse: collapse;
  font-size: 14px;
}

.Orders-list th {
  text-align: left;
}

.Orders-list th,
.Orders-list td {
  padding: 13px 8px;
  border-bottom: 1px solid #ccc;
}

.Orders-date {
  white-space: nowrap;
}

.Orders-description {
  color: #555;
}

@media (max-width: 549px) {
  .Orders-description--desktop {
    display: none;
  }
}

@media (min-width: 550px) {
  .Orders-description--mobile {
    display: none;
  }
}

.OrderDetail {
  max-width: 960px;
}

.Section-heading {
  margin: 0 0 20px;
  text-transform: uppercase;
  color: #333;
  font-weight: 600;
  font-size: 28px;
}

.Section-header {
  margin-bottom: 20px;
}

.Section-header > .Section-heading {
  margin-bottom: 0;
}

